* {
    transition: all .5s;
}

#StageFourWrapper {
    min-height: 25%;
    border-radius: 10px;
    background-color: #FFF;
    width: 90%;
    max-width: 500px;
    position: absolute;
    top: 133.4px;
    left: 50%;
    transform: translateX(-50%);
    -webkit-box-shadow: 0px 10px 70px -40px rgba(0,0,0,0.50);
    -moz-box-shadow: 0px 10px 70px -40px rgba(0,0,0,0.50);
    box-shadow: 0px 10px 70px -40px rgba(0,0,0,0.50);
    padding-bottom: 1rem;
}

#StageFourWrapper > * {
    width: 85%;
    margin-left: auto;
    margin-right: auto;
}

#StageFourHeading {
    font-size: 1.5rem;
    font-weight: 700;
    color: #022959;
    margin-top: 2rem;
    margin-bottom: .5rem;
}

#StageFourParagraph {
    font-size: 1rem;
    font-weight: 400;
    color: #9699AA;
    margin-bottom: 1.5rem;
    margin-top: 0;
}

#StageFourDetailsWrapper {
    background-color: #F8F9FF;
    padding-bottom: 1rem;
    padding-top: 1rem;
}

#StageFourPlanDiv {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .6rem 0 .5rem 0;
    border-bottom: 1px solid transparent;
    margin: 0 1rem;
}


.flexDiv {
    display: flex;
    align-items: center;
    justify-content: center;
}

.StageFourAddonDiv {
    margin: 0 1rem;
    padding:0;
    font-size: 0.875rem;
    font-weight: 400;
    color: #9699AA;
}

.StageFourAddoname {
    position: absolute;
    left: 0;
}



#FinishingAddonOne {
    height: 0px;
    line-height: 0px;
    padding: 0;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#FinishingAddonTwo {
    height: 0px;
    line-height: 0px;
    padding: 0;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#FinishingAddonThree {
    height: 0px;
    line-height: 0px;
    padding: 0;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#StageFourAddonsDiv{
    margin: 0;
    padding: 0;
}

.StageFourAddonPrice {
    font-size: 0.875rem;
    font-weight: 400;
    color: #022959;
    padding: 0;
    min-width: 1px;
    /*position: absolute;
    right: 0;*/
}


#StageFourPlanTitle {
    font-size: 0.875rem;
    font-weight: 500;
    color: #022959;
    margin: 0;
    padding: .5rem 0 .25rem 0;
}

#StageFourChangeLink {
    font-size: 0.875rem;
    font-weight: 400;
    color: #9699AA;
    text-decoration: underline;
    text-underline-offset: 2px;
    margin: 0;
    padding: .25rem 0 .5rem 0;
    cursor: pointer;
}

#StageFourChangeLink:hover {
    color: #483EFF;
    transition: all 1s;
}

#StageFourPlanPrice {
    font-size: 0.875rem;
    font-weight: 700;
    color: #022959;
}

#StageFourTotalDiv {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 1rem;
    margin-top: .5rem;
}

#TotalText {
    font-size: 0.875rem;
    font-weight: 400;
    color: #9699AA;
}

#TotalPrice {
    font-size: 1rem;
    font-weight: 700;
    color: #483EFF;
}

@media screen and (min-width: 1023.5px) {
    #StageFourWrapper {
        position: static;
        transform: translateX(0%);
        display: block;
        width: 75%;
        max-width: none;
        margin: auto;
        box-shadow: none;
    }

    #StageFourParagraph {
        margin-bottom: 2rem;
    }

}